@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: sans-serif;
  background: -webkit-gradient(linear, left top, right top, from(#e67e22), to(#e74c3c));
  background: -webkit-linear-gradient(left, #e67e22, #e74c3c);
  background: linear-gradient(to right, #e67e22, #e74c3c);
}

.wrapper {
  background: -webkit-gradient(linear, left top, right top, from(#f8ca97), to(#ef747c));
  background: -webkit-linear-gradient(left, #f8ca97, #ef747c);
  background: linear-gradient(to right, #f8ca97, #ef747c);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}



.overlay{
  background-color: white;
  opacity: 0.3;
  position: absolute;
  width:100%;
  height: 100%;

}


.main {
  height: 90vh;

  width: 80%;
  margin: 0 auto;
}

.row1 {
    margin-right: 0px;
    margin-left: 0px;
}

.title {
  height: 20vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #2b3e50;
  padding-top: 5px;
  padding-bottom: 5px;
  margin:0px !important;


}

.title-container {
  height: 70vh;
  background: url(/static/media/bg.6fd7e2fc.png) center center no-repeat;
  background-size: cover;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: #000;
}

.title-container__weather_title {
  font-size: 30px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: sans-serif;
  color: white;
  min-width: 80px;
  text-shadow: 2px 2px 4px #000000;
}
.center{
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.title-container__title {
  font-size: 50px;
  letter-spacing: 2px;
  line-height: 1.3;
  font-family: sans-serif;
}

.title-container__subtitle {
  font-style: italic;
  font-weight: 100;
  font-size: 1.2em;
  letter-spacing: 1px;
  line-height: 1.5;
  font-family: sans-serif;
}

.form-container {
  background-color: #2c3e50;
  height: 70vh;
  padding-top: 100px;
  padding-left: 50px;
  
}

input[type="text"] {
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #ef777d;
  padding-bottom: 4px;
  color: #fff !important;
  font-weight: lighter;
  letter-spacing: 2px;
  margin-bottom: 30px;
  margin-right: 20px;
  font-size: 20px;
}

input[type="text"] { 
    outline: none;
}

input:-webkit-autofill {
    -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
    -webkit-text-fill-color: #fff !important;
}

button {
    border: 0;
    padding: 8px 20px;
    margin: 0 2px;
    border-radius: 2px;
    font-weight: lighter;
    letter-spacing: 1px;
    font-size: 15px;
    cursor: pointer;
    background-color: #ef777d;
    color: #2b3e50;
    font-weight: 100;
}

button:active {
  outline: none;
}

.weather__info {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px;
}

.weather__key {
  color: #ef777d;
  border-bottom: solid 2px rgba(255,255,255,0.06);
  padding: 20px 0 20px 0;
  font-weight: 400;
}

.weather__key:last-child {
  border: 0;
}

.weather__value {
  color: #fff;
  font-weight: 200;
}

.weather__error {
  color: #f16051;
  font-size: 20px;
  letter-spacing: 1px;
  font-weight: 200;
}
